.page2{
    position: relative;
    color: $white;
    background: $color1;
    box-shadow: 0 0 30px 30px $color1;
    &::after{
        content: "";
        position: absolute;
        width: 100%;
        height: 60%;
        background: url(../../assets/img/wepik-export-20231226084246GkEo.jpeg) fixed center/cover ;
        filter:  brightness(0.8) grayscale(40%);
        left: 0;
        top: 40%;
        clip-path: polygon(50% 10%, 100% 0%, 100% 100%, 0 100%, 0 0);
        @media screen and (max-width:500px) {
            top: 32%;
            height: 68%;
        }
    }


    .page2-container{
        max-width: 1300px;
        margin: 0 auto;
        display: flex;
        padding: 100px 20px 50px;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        position: relative;
        cursor: default;

        .hexagon{
            position: absolute;
            z-index: 10;
            transform: translate(-50%,-50%);
            background:$color1;
            filter: brightness(0.7) grayscale(50%);  
            clip-path: polygon(50% 0, 100% 22%, 100% 75%, 50% 99%, 0 75%, 0 22%);
            animation: hexagon 30s infinite;
            @media screen and (max-width:850px) {
                animation: hexagon 25s infinite;
            }
            
            &:nth-child(1){
                width: 350px;
                height: 420px;
                top: -50px;
                right: 50px;
                @media screen and (max-width:850px) {
                    width: 270px;
                    height: 324px;
                }

                @media screen and (max-width:600px) {
                    width: 220px;
                    height: 264px;
                    top: -30px;
                }
                @media screen and (max-width:500px) {
                    width: 180px;
                    height: 216px;
                }
            }
            &:nth-child(2){
                width: 250px;
                height: 300px;
                bottom:150px;
                left: 50px;
                animation-delay: 1s;
                @media screen and (max-width:850px) {
                    width: 200px;
                    height: 240px;
                }
                @media screen and (max-width:600px) {
                    width: 160px;
                    height: 192px;
                    bottom: 30px;
                }
                @media screen and (max-width:500px) {
                    bottom: -10px;
                    left: 40px;
                }
            }
            
            @keyframes hexagon {
                0%{
                   transform: scale(1);
                }
                50%{
                    transform: scale(1.2);
                }
                100%{
                    transform: scale(1);
                }
            }
        }


        .cards-container1{
            display: flex;
            gap: 15px;
            z-index: 100;
            position: relative;
        }
        .cards-container2{
            display: flex;
            gap: 15px;
            transform: translateY(-20%);
            position: relative;
            z-index: 100;
        }
        .card{
            clip-path: polygon(50% 0, 100% 22%, 100% 75%, 50% 99%, 0 75%, 0 22%);
            width: 240px;
            height: 270px;
            position: relative;
            transition: 0.25s ease-in;

            @media screen and (min-width:1300px) {
                width: 300px;
                height: 337.5px;
            }
            @media screen and (max-width:850px) {
                width: 200px;
                height: 225px;
            }
            @media screen and (max-width:700px) {
                width: 175px;
                height: 196.875px;
            }
            @media screen and (max-width:600px) {
                width: 140px;
                height: 157.5px;
            }
            @media screen and (max-width:480px) {
                width: 100px;
                height: 112.5px;
            }
            

            &:hover{
                transform: scale(1.05);
            }

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                filter: grayscale(40%) brightness(0.5);
            }
            .card-text{
                position: absolute;
                top: 50%;
                left: 50%;
                @media screen and (max-width:700px) {
                   font-size: 0.8rem;
                }
                @media screen and (max-width:600px) {
                    font-size: 0.7rem;
                    top: 40%;
                 }
                 @media screen and (max-width:500px) {
                    font-size: 0.5rem;
                    top: 35%;
                 }

                transform: translateX(-50%);
                button{
                    margin-top: 15px;
                    @media screen and (max-width:700px) {
                        font-size: 0.75rem;
                     }
                     @media screen and (max-width:600px) {
                        font-size: 0.65rem;
                        border: 1.3px solid $white;
                        margin-top: 10px;
                     }
                     @media screen and (max-width:500px) {
                        font-size: 0.55rem;
                        border: 1.3px solid $white;
                        margin-top: 15px;
                        padding:3px 5px;
                     }
                }
            }
        }
    }
    .text-infos{
        width: 65%;
        margin:0 auto 25px;
        font-family: $font1;
        font-size: 1.15rem;
        line-height: 22px;
        letter-spacing: 3px;
        position: relative;
        padding-top: 50px;
        z-index: 100;
        cursor: default;
        max-width: 1300px;
        margin: 0 auto;
        @media screen and (max-width:700px) {
            font-size: 1rem;
         }

        &::before{
            content: "";
            position: absolute;
            width: 120%;
            height: 2px;
            background: $white;
            top: 0;
            left: -10%;
        }
    }
}

