.footer{
    text-align: left;
    padding: 60px 50px 5px;
    font-family: $font1;
    position: relative;
    z-index: 100;
    cursor: default;
    max-width: 1300px;
    margin: 0 auto;

    .footer-container{
        display: grid;
        grid-template-columns: repeat(5,1fr);
        align-items: baseline;
        justify-content: center;
        margin-bottom: 60px;
        z-index: 2;
        @media screen and (max-width:900px) {
            grid-template-areas: "a b e" "c d e";
            grid-template-columns: 1fr 1fr 1fr;
            gap: 50px;
         }
         @media screen and (max-width:800px) {
            gap: 20px;
        }
        @media screen and (max-width:600px) {
            grid-template-areas: "a b" "c d" "e e";
            grid-template-columns: 1fr 1fr;
            gap: 20px;
        }

        h3{
            margin-bottom: 15px;
            @media screen and (max-width:1050px) {
            font-size: 0.8rem;
            }
            @media screen and (max-width:500px) {
                font-size: 0.65rem;
                }
            
        }
        .contain{
            border-right: 1px solid $white;
            height: 180px;
            padding: 0 0 0 15px ;
            @media screen and (max-width:900px) {
                height: 150px;
                padding: 0;
            }
            @media screen and (max-width:600px) {
                padding-left: 20px;
                &:nth-child(2){
                    border: none;
                }
                &:nth-child(4){
                    border: none;
                }
            }
            ul{
                display: flex;
                flex-direction: column;
                gap: 8px;
                color: rgba(255, 255, 255, 0.8);
                li{
                    transition: 0.25s ease-in;
                    @media screen and (max-width:1050px) {
                        font-size: 0.77rem;
                    }
                    cursor: pointer;
                    &:hover{
                        transform: scale(1.1) translateX(8px);
                    }
                }
            }
        }
        .logo-footer{
            padding: 0 0 0 15px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            text-align: center;
            gap: 30px;
            @media screen and (max-width:900px) {
                margin:auto 0;
                padding: 0;
                grid-area: e;
            }
            .logo{
                margin-right: 0;
            }
        }
    }

    .signature-infos{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        align-items: center;
        justify-content: center;
        text-align: center;
        padding: 10px 0 15px;
        color:rgba(255, 255, 255, 0.7);
        @media screen and (max-width:800px) {
            grid-template-columns: 1fr;
            gap: 20px;
        }

        p{
            font-size: 0.6rem
        }

        .terme{
            display: flex;
            gap: 10px;
            margin-left: 0;
            align-items: center;
            justify-content: center;

            li{
                border-right: 1px solid;
                padding-right: 20px;
                font-size: 0.6rem;
                cursor: pointer;
                &:last-child{
                    border: none;
                    padding: none;
                }
            }
        }
        span{
          color: $white;  
          font-size: 0.9rem;
        }
    }
}