.header{
    padding: 5px 10px;
    display: grid;
    grid-template-columns: 20% 55% 25%;
    align-items: center;
    justify-content: center;
    background: #0000000a;
    cursor: default;
    max-width: 1300px;
    margin: 0 auto;
    @media screen and (max-width:1000px) {
        grid-template-columns: 20% 45% 35%;
    }
    @media screen and (max-width:850px) {
        grid-template-columns: 20% 60% 20%;
    }    
    @media screen and (max-width:700px) {
        grid-template-columns: 1.3fr 0.7fr;
    }    


    .nav{
        display: flex;
        justify-content: space-evenly;
        align-items: end;
        font-family: $fontM;
        text-transform: uppercase;
        color: $white;
        li{
            cursor: pointer;
            transition: 0.25s ease-in;
            @media screen and (max-width:1100px) {
                font-size: 0.8rem;
            }
            @media screen and (max-width:500px) {
                font-size: 0.7rem;
            }    

            &:hover{
                transform: scale(1.1);
            }
        }
    }
    .panier{
        display: flex;
        justify-content: end;
        gap: 25px;
        margin-right: 15px;

        button:nth-child(1){
            border-right: none;
        }
        button{
            border: 1px solid;
            @media screen and (max-width:1100px) {
                font-size: 0.8rem;
            }
        }

    }
    .icon{
        @media screen and (max-width:850px) {
            display: none;
        }    
    }
    .logo{
        @media screen and (max-width:700px) {
            display: none;
        }    
    }
}
.logo{
    margin-right: 50px;
    img{
        width: 100px;
        height: 40px;
        object-fit: cover;
        padding-top: 15px;
        @media screen and (max-width:1000px) {
            width: 80px;
        }
    
    }
    h2{
        font-size: 0.7rem;
        margin-top: 5px;
        @media screen and (max-width:1000px) {
            font-size: 0.6rem;
        }
    
    }
}
.icon{
    display: flex;
    align-items: center;
    gap: 15px;
    li{
        color: $white;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 28px;
        height: 16px;
        padding:0;
        position: relative;
        border: 2px solid rgba(255, 255, 255, 0.7);
        border-top: none;
        border-bottom: none;
        cursor: pointer;
        transition: 0.25s ease-in;

        @media screen and (max-width:1100px) {
            width: 24.5px;
            height: 14px;
        }


        &:hover{
            transform: scale(1.1);
        }

        &::before{
            content: "";
            position: absolute;
            top: 0;
            left: -2px;
            width: 100%;
            height: 100%;
            border: 2px solid rgba(255, 255, 255, 0.75);
            transform: rotate(120deg);
            border-top: none;
            border-bottom: none;
        }
        &::after{
            content: "";
            position: absolute;
            top: 0;
            left: -2px;
            width: 100%;
            height: 100%;
            border: 2px solid rgba(255, 255, 255, 0.75);
            transform: rotate(-120deg);
            border-top: none;
            border-bottom: none;
        }

    }           
}